import axios        from '../util/axios';
import { baseUrl }  from './config';

// 请求时间列表
export function getTimeList(data) {
    let url = baseUrl + '/App/Card/getHistory';
    return axios.get(url, {
        params: {
            ...data
        }
    }).then(res => { return Promise.resolve(res) })
}

// 请求行为数据列表
export function getActionInfo(data) {
    let url = baseUrl + '/App/Card/getCadAccessData';
    return axios.get(url, {
        params: {
            ...data
        }
    }).then(res => { return Promise.resolve(res) })
}

// 请求行为数据列表
export function getPeopleList(data) {
    let url = baseUrl + '/App/Card/getCardUserData';
    return axios.get(url, {
        params: {
            ...data
        }
    }).then(res => { return Promise.resolve(res) })
}



